.cart {
    width: 90px;
    flex: 0 1 auto;
}

.description {
    margin: 20px 0;
}

/* encapsulates the entire navigation bar */
.header-bar {
    align-items: center; 
    width: 75%;
    display: flex;
    margin: auto;
}

.header-bar-text {
    display: flex;
    margin: 20px;
}

.highlight:hover {
    background-color: yellow;
    font-weight: 700;
}

.line {
    flex: 1 0 auto;
}

.line-weight {
    border: none;
    height: 4px;
    background-color: #333;
}

/* Bun Bun shop logo formatting */
.logo {
    flex: 0 0 auto; 
    width: 200px; 
    height: 200px;
    margin: 0 30px;
}

.products {
    flex: 1 0 auto;
    text-align: right;
    margin: 0 100px 0 0;
}
